/* landing page css starts here*/
#imgVid{
  text-align: center;
}

#imgVid h1{
  background-position: center;
  background-repeat: no-repeat; 
  background-size: cover;
  background-image: url("./img/main-image.jpg");
  color:white;
  height: auto;
  width: 100%;
  margin:auto;
  padding-top: 15%;
  padding-block: 15%;
}

#imgVid iframe{
  height:400px;
  width: 600px;
  padding:1%;
  border: none;
}

.breaker h3{
	width: 90%;
	margin: .7em auto;
	overflow: hidden;
	text-align: center;
	padding-top:2%;
}

.breaker h3:before, h3:after{
	content: "";
	display: inline-block;
	width: 50%;
	margin: 0 .5em 0 -55%;
	vertical-align: middle;
	border-bottom: 2px solid;
}

.breaker h3:after {
  margin: 0 -55% 0 .5em;
}

.gridItems{
  width:80%;
  margin:auto;
  text-align: center;
}
.gridItems img{
  width:80%;
  height:auto;
  padding-top: 9%;
}

.gridItems div:hover{
  cursor: pointer;
  text-decoration: underline;
}
/* landing page css ends here*/

.nameImg:hover{
  cursor: pointer;
  text-decoration: underline;
}

/*/////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////*/
/*////////////////////////////////////////////////////////////  @media queries starts from here ///////////////////////////////////////////////////////////////////////////*/
/*/////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////*/

@media screen and (max-width: 600px) {
  #banner{
    width:90% !important;
    height:auto !important;
  }

  #imgVid iframe{
    height:150px;
    width:300px;
  }

  .nameImg{
    display: block;
    margin: none;
  }

}